body {
  margin: 0;
  padding: 0;
  font-family: "FuturaPT-Book";
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  width: 100%;
  background-color: #282c34;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10vh);
  color: white;
  justify-content: space-between; 
  padding: 2vh 16vw;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app {
  display: flex;
  flex-direction: column;
}

.code-editor {
  width: 100vw;
  flex: 1;
  margin: 5vh;
}

.ce {
  margin: 10vh;
  display: flex;
  flex-direction: row;
}

.preview {
  width: 50vw;
  flex: 1;
  border-left: 1px solid #fff;
  padding: 20px;
  margin: 5vh;
  color: white;
}

@media (max-width: 767px) {
  .ce {
    flex-direction: column;
  }

  .preview {
    border-left: none;
    border-top: 1px solid #fff;
  }
}

button {
  cursor: pointer;
  background-color: #00babc; /* Couleur des boutons */
  color: white; /* Couleur du texte des boutons */
  border: none;
  border-radius: 10px;
  width: 10vw;
  height: 5vh;
  font-size: 2vh;
  transition: transform 0.3s ease-in-out;
}

button:hover {
  background-color: #475b67;
  transform: scale(1.2);
}

button:disabled {
  background-color: #475b67;
}

.hero {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 10vh;
    display: flex;
    margin-bottom: 5vh;
    z-index: 10000;
    background-color: #173d7a;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  background-color: #444;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  z-index: 9999;
  color: white;
}

.boutons {
  display: flex;
  justify-content: space-between;
}

.logos {
  position: relative;
  display: flex;
  justify-content: space-around;
  margin: 0 5vw;
}

.logos img {
  position: relative;
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 5vh;
  display: flex;
  margin-top: 5vh;
  z-index: 10000;
  background-color: #282c34;
  color: white;
  align-items: center;
  justify-content: center;
  justify-content: space-around;
}
